import React from 'react';

import {graphql} from 'gatsby';

import Layout from '../../components/layout';
import TopColleges from '../../components/TopColleges';

type DataProps = {
  data: {
    allStrapiCollege: {
      nodes: any;
    };
    allRanks2021Json: {
      nodes: any;
    };
  };
};

const TopCollegesRamanathapuram: React.FC<DataProps> = ({data}) => {
  const {allRanks2021Json} = data;
  return (
    <Layout backLink="/" headerTitle="Top Ramanathapuram Colleges">
      <TopColleges data={allRanks2021Json} location="Ramanathapuram" />
    </Layout>
  );
};

export const query = graphql`
  query {
    allRanks2021Json(filter: { con: { regex: "/.*Ramanathapuram District.*/" } }) {
      nodes {
        con
        coc
        BC
        BCM
        MBC
        MBCDNC
        MBCV
        OC
        SC
        SCA
        ST
        id
        brc
        brn
      }
    }
  }
`;

export default TopCollegesRamanathapuram;
